html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  line-height: 1.8;
}

a {
  text-decoration: none;
}

p {
  margin: 0;
}

img {
  width: 100%;
  display: block;
}

.container {
  width: 90%;
  margin: 2rem auto;
}
.container h3 {
  text-align: center;
  text-transform: uppercase;
  font-size: 2.5rem;
  font-weight: 400;
  padding: 0;
  margin: 1rem;
  color: #488704;
}

.container_cite {
  display: grid;
  grid-template-columns: 1fr 5fr;
  gap: 0;
}

.alert {
  width: 100%;
  padding: 1rem;
  color: #FFFFFF;
  text-transform: capitalize;
  margin: 0.5rem 0;
  text-align: center;
  font-size: 2rem;
  position: absolute;
}

.error {
  background-color: red;
}

.success {
  background-color: #488704;
}

.danger {
  width: 100%;
  margin: 1vh auto;
  background-color: rgba(255, 0, 0, 0.8);
  text-align: left;
  border-radius: 0.3vh;
  border-color: red;
}

.info {
  width: 95%;
  margin: 1vh auto;
  background-color: rgba(0, 99, 202, 0.8);
  text-align: left;
  border-radius: 0.3vh;
  border-color: rgb(0, 99, 202);
}

.alert-mn {
  width: 50%;
  margin: 0.5rem auto;
}

.alert-md {
  width: 90%;
  margin: 0.5rem auto;
}

.btn-tb {
  width: 100%;
  height: 5vh;
  background-color: #488704;
  margin: 2vh auto;
  padding: 1vh 1vh;
  color: white;
  text-transform: capitalize;
  text-decoration: none;
}

.btn {
  padding: 0.5rem;
  width: 20%;
  border: none;
  background-color: #488704;
  margin-top: 2rem;
  color: white;
  font-size: 1.8rem;
  font-weight: bold;
  display: inline-block;
  text-align: center;
}
.btn:hover {
  background-color: rgba(72, 135, 4, 0.8);
  color: #000000;
  cursor: pointer;
}

.btn-register {
  height: 6vh;
  margin: 0;
  width: 25%;
}

.btn-min {
  border: 0.2vh solid #FFFFFF;
  margin: 1vh;
  width: 30%;
  padding: 0.2vh;
}

.update {
  background-color: rgb(0, 99, 202);
}
.update:hover {
  background-color: rgba(0, 99, 202, 0.8);
  color: #FFFFFF;
  cursor: pointer;
}

.delete {
  background-color: red;
}
.delete:hover {
  background-color: rgba(255, 0, 0, 0.8);
  color: #FFFFFF;
  cursor: pointer;
}

