/** Media Queries **/
.header {
  width: 100%;
  padding: 1rem;
  background-position: center center;
  background-color: #488704;
}
.header .header_container {
  width: 90%;
  height: 11vh;
  margin: auto;
}
.header .header_container .home {
  line-height: 11vh;
  font-size: 5vh;
  color: #FFFFFF;
  font-weight: bold;
}
.header .header_container .navbar {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
.header .header_container .navbar .navbar_nav {
  width: 100%;
}
.header .header_container .navbar .navbar_nav ul {
  display: flex;
  justify-content: end;
}
.header .header_container .navbar .navbar_nav ul li {
  list-style: none;
  margin-right: 2vh;
  padding: 0.5vh;
}
.header .header_container .navbar .navbar_nav ul li a {
  color: #FFFFFF;
  font-size: 3vh;
  padding: 3vh;
}
.header .header_container p {
  height: 11vh;
  width: 100%;
  margin-top: 70vh;
  font-size: 5vh;
  padding: 1vh 1vh;
  color: #FFFFFF;
}

.inicio {
  background-image: url(../img/hospital.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  height: 60rem;
}

.footer {
  padding: 3rem;
  background-color: #488704;
  text-align: center;
  text-transform: uppercase;
  font-size: 2rem;
  color: #FFFFFF;
}

.footer-mg {
  margin-top: 0;
  border-top: 0.1rem solid #FFFFFF;
}

.noticias {
  width: 100%;
  height: auto;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
}
.noticias .noticia {
  width: 100%;
  height: auto;
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  border: 0.2vh solid #488704;
}
.noticias .noticia img {
  height: 100%;
  width: 100%;
}
.noticias .noticia p {
  width: 100%;
  height: 40vh;
  text-align: justify;
}

.mainForm {
  width: 100%;
  height: auto;
  display: grid;
  grid-template-columns: 1.5fr 1fr;
}
.mainForm .background-image {
  background-image: url(/build/img/hospital.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100% 100%;
}
.mainForm section {
  width: 100%;
  height: auto;
  position: relative;
}
.mainForm section h1 {
  text-align: center;
  color: #488704;
  text-transform: capitalize;
}
.mainForm section .alert_float {
  margin: -5vh 2.5vh;
}

.formulario {
  width: 95%;
  margin: 0 auto 2vh;
  color: #488704;
  text-transform: capitalize;
  height: auto;
}
.formulario .content-input {
  margin-bottom: 1rem;
}
.formulario .content-input label {
  display: block;
  font-size: 1.9rem;
  color: #488704;
}
.formulario .content-input input, .formulario .content-input select {
  width: 100%;
  padding: 0.8rem;
  margin: 0vh auto;
  color: #488704;
  outline-style: none;
  font-size: 1.8rem;
  text-transform: capitalize;
  height: 6vh;
  border-radius: 0.3vh;
  border-color: #488704;
}
.formulario .content-input input::placeholder, .formulario .content-input select::placeholder {
  color: #488704;
  text-transform: capitalize;
}
.formulario .content-input select {
  appearance: none;
  border: 0.1vh solid #488704;
  background-color: #FFFFFF;
  border-radius: 0.3vh;
}
.formulario .check {
  display: inline-block;
}
.formulario p {
  font-size: 2rem;
  display: inline-block;
  width: 30%;
  display: inline;
  margin-left: 2rem;
}

.min-w {
  width: 50%;
  margin: 2rem auto 3rem;
}

.md-w {
  height: 50vh;
  margin: 14vh auto;
}
.md-w h1 {
  margin: 0 auto 3vh;
  text-align: center;
}

.user {
  width: 100%;
  height: 11vh;
  color: #FFFFFF;
  line-height: 11vh;
  text-align: end;
  font-size: 5vh;
  font-weight: bold;
  text-transform: capitalize;
}

.main {
  width: 100%;
  display: grid;
  height: auto;
  grid-template-columns: 20% 80%;
}
.main .navbar {
  width: 100%;
  height: 120vh;
  margin: 0;
  background-color: #488704;
  border-bottom: 0.2vh solid #FFFFFF;
}
.main .navbar ul {
  width: 100%;
  margin: 0;
  height: auto;
  padding: 0;
}
.main .navbar ul li {
  margin-left: 0;
  list-style: none;
  width: 100%;
  padding: 2vh;
  text-align: left;
  border-top: 0.2vh solid #FFFFFF;
}
.main .navbar ul li a {
  color: #FFFFFF;
  font-size: 3vh;
}
.main .navbar ul li:nth-child(6) {
  border-bottom: 0.2vh solid #FFFFFF;
}

.form {
  width: 95%;
  height: auto;
  margin: 5vh auto;
  border: 0.2vh solid #488704;
}
.form .form_header {
  background-color: #488704;
  height: 8vh;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
.form .form_header h1 {
  margin: 0;
  font-size: 4vh;
  color: #FFFFFF;
}
.form .form_header .actions {
  display: flex;
  justify-content: end;
}
.form .form_body {
  height: 85vh;
}
.form .form_body .container_input {
  margin: 4vh auto;
  width: 95%;
  height: 8vh;
}
.form .form_body .container_input label {
  display: block;
  font-size: 1.9rem;
  color: #488704;
}
.form .form_body .container_input input, .form .form_body .container_input select {
  width: 100%;
  padding: 0.8rem;
  color: #488704;
  outline-style: none;
  font-size: 1.8rem;
  text-transform: capitalize;
  height: 6vh;
  border-radius: 0.3vh;
  border-color: #488704;
}
.form .form_body .container_input input::placeholder, .form .form_body .container_input select::placeholder {
  color: #488704;
  text-transform: capitalize;
}
.form .form_body .container_input select {
  appearance: none;
  border: 0.1vh solid #488704;
  background-color: #FFFFFF;
  border-radius: 0.3vh;
}
.form .twoColums {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
.form .form_footer {
  height: 8vh;
  background-color: #488704;
  text-align: center;
  font-size: 4vh;
  color: #FFFFFF;
}

table {
  width: 95%;
  height: auto;
  margin: 5vh auto;
  border: 0.2vh solid #488704;
  border-collapse: collapse;
}
table caption {
  background-color: #488704;
  text-align: left;
  color: #FFFFFF;
  font-size: 4vh;
  height: 8vh;
}
table thead {
  height: 5vh;
  font-size: 3vh;
  background-color: gray;
  text-align: center;
  color: #FFFFFF;
  font-weight: 400;
}
table tbody {
  text-align: center;
  font-size: 2.8vh;
  width: 100%;
  text-transform: capitalize;
  color: #488704;
}
table tfoot {
  height: 8vh;
  background-color: #488704;
}

.information {
  width: 100%;
  height: auto;
}